import Vue from 'vue'
import Vuex from 'vuex'
// 用于存放和获取用户cookie
import { setItem, getItem, delItem } from '@/utils/uUser'

Vue.use(Vuex)

// TODO 为了防止传参是本地存储的key写错了（是不会错的！）所以把key单独定义调用，就不需要传递了！
const USER_KEY = 'token'

export default new Vuex.Store({
  state: {
    // 用于存储用户登陆信息， 获取本地存储的cookie
    // token: JSON.parse(document.cookie)
    token: getItem(USER_KEY),
    // 用于存储tabbar组件，用于控制缓存
    iscould: ['TabbarIndex']
  },
  mutations: {
    setToken (state, uToken) {
      // 为了防止页面刷新，数据丢失，需要将数据存储到本地
      // document.cookie = JSON.stringify(state.token)
      state.token = uToken // 为了让页面实时刷新用（从本地获取 页面不能实时刷新）
      setItem(uToken, USER_KEY)
    },
    delToken (state) {
      delItem(USER_KEY)
      state.token = null // 为了让页面实时刷新用（从本地获取 页面不能实时刷新）
    },
    // 添加缓存组件(需要页面缓存时就调用该方法，传递需要缓存的页面name)
    addCould (state, pageName) {
      // 缓存组件数组中没有该页面就添加进来
      if (!state.iscould.includes(pageName)) {
        state.iscould.push(pageName)
      }
    },
    // 删除缓存组件
    removeCould (state, pageName) {
      // 缓存组件数组中有该组件就从缓存数组中移除该组件name
      const index = state.iscould.indexOf(pageName)
      if (index !== -1) {
        state.iscould.splice(index, 1)
      }
    }
  },
  actions: {
  },
  modules: {
  }
})
