import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 引入lib-flexible用于自动设置rem基准值
import 'amfe-flexible'
// 引入vant-ui组件库和样式
import Vant from 'vant'
import 'vant/lib/index.css'
// 引入全局统配样式文件 （自定义样式放在最后，方便覆盖vant第三方样式）
import './styles/index.css'
// 引入时间处理工具模块（里面注册有全局时间处理的过滤器days）
import '@/utils/dayjs'
// 统一优化css的工具
import 'normalize.css'

// 注册vant-ui组件库
Vue.use(Vant)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
