import Vue from 'vue'
import VueRouter from 'vue-router'
// 引入vuex
import store from '@/store/index'
// 引入vant确认弹窗
import { Dialog } from 'vant'

const LoginIndex = () => import('@/views/login/index.vue')
const TabberIndex = () => import('@/views/tabbar/index.vue')
const HomeIndex = () => import('@/views/home/index.vue')
const QaIndex = () => import('@/views/qa/index.vue')
const VideoIndex = () => import('@/views/video/index.vue')
const MyIndex = () => import('@/views/my/index.vue')
const search = () => import('@/views/home/search')
const article = () => import('@/views/article/index.vue')
const setMy = () => import('@/views/my/SetMy/index.vue')
const userCart = () => import('@/views/my/userCart/index.vue')

Vue.use(VueRouter)

const routes = [
  // 登陆注册页为路由组件
  {
    path: '/login',
    name: 'login',
    component: LoginIndex,
    meta: {
      onlog: false
    }
  },
  // 把底部导航栏注册为路由组件
  {
    path: '/',
    name: 'TabbarIndex',
    component: TabberIndex,
    meta: {
      onlog: false
    },
    // 导航的页面注册为子路由
    children: [
      // 首页 (默认子路由)
      {
        path: '/',
        name: 'HomeIndex',
        component: HomeIndex,
        meta: {
          onlog: false
        }
      },
      // 问答页
      {
        path: 'qa',
        name: 'qa',
        component: QaIndex,
        meta: {
          onlog: true
        }
      },
      // 视频页
      {
        path: 'video',
        name: 'video',
        component: VideoIndex,
        meta: {
          onlog: true
        }
      },
      // 我的页
      {
        path: 'my',
        name: 'my',
        component: MyIndex,
        meta: {
          onlog: false
        }
      }
    ]
  },
  // 搜索页面
  {
    path: '/search',
    name: 'search',
    component: search,
    meta: {
      onlog: false
    }
  },
  // 文章详情页
  {
    // 动态传每个文章的id
    path: '/article/:articleId',
    name: 'article',
    component: article,
    props: true, // 开启props传参
    meta: {
      onlog: false
    }
  },
  // 个人设置页面
  {
    path: '/setMy',
    name: 'setMy',
    component: setMy,
    meta: {
      onlog: false
    }
  },
  // 小智同学 聊天机器人
  {
    path: '/my/userCart',
    name: 'userCart',
    component: userCart,
    meta: {
      // 用于控制页面是否需要登录
      onlog: true
    }
  }
]

const router = new VueRouter({
  routes
})

// 前置导航守卫
router.beforeEach((to, from, next) => {
  // 判断是否需要拦截处理
  if (to.meta.onlog) {
    // 判断登录没有
    if (!store.state.token) {
      // 确认弹出框
      Dialog.confirm({
        message: '该内容需要登录，是否登录？'
      }).then(() => {
        // 没登录就跳转登录页
        router.replace({
          name: 'login',
          query: {
            r: router.currentRoute.fullPath
          }
        })
      }).catch(() => {
        next(false)
      })
    } else {
      // 登录了就直接前往目标页
      next()
    }
  } else {
    next()
  }
})

export default router
