// 初始化dayjs相关配置的模块

// 加载dayjs、中文语言包、relativeTime(相对时间插件)
import * as dayjs from 'dayjs'
import 'dayjs/locale/zh-cn'
import relativeTime from 'dayjs/plugin/relativeTime'
// 引入vue用于注册全局过滤器
import Vue from 'vue'

dayjs().format()

dayjs.locale('zh-cn') // 全局使用简体中文

dayjs.extend(relativeTime) // 配置使用相对时间API

console.log(dayjs('2018-11-29T17:09:52').from(dayjs()))

// 把处理相对时间的功能封装成一个全局过滤器，方便使用 (直接传递进来一个格式化时间就好  直接这么写就好{{数据 | days}})
Vue.filter('days', value => {
  return dayjs(value).from(dayjs())
})

// 导出调用相对时间（第二种方法 导出引入调用）
// export default times => {
//   return dayjs(times).from(dayjs())
// }

// 把处理时间格式化的功能封装成一个全局过滤器，方便使用 (直接传递进来一个格式化时间就好  直接这么写就好{{数据 | days}})
Vue.filter('daysFormat', (value, gs = 'YYYY-MM-DD HH:mm') => {
  return dayjs(value).format(gs) // format是dayjs格式化时间的方法
})
